import {hashSHA256, X} from "./hashSHA256";
import { db } from "./firebase";
import {doc, deleteDoc, collection, getDocs, addDoc, updateDoc} from "firebase/firestore";

export const checkHash = async t => {
    const V = await hashSHA256(t);
    return V === X;
};

export const addOrg = async (orgData) => {
    try {
        const orgCollection = collection(db, "organizations");
        const docRef = await addDoc(orgCollection, orgData);
        console.log(`Добавлена организация, ID: ${docRef.id}`);
        return docRef.id; // Возвращает ID добавленного документа
    } catch (error) {
        console.error("Произошла ошибка при добавлении: ", error);
        throw error; // Для обработки ошибок
    }
};

export const removeOrg = async (orgId) => {
    try {
        const orgDoc = doc(db, "organizations", orgId);
        await deleteDoc(orgDoc);
        console.log(`Организация с ID: ${orgId} успешно удалена.`);
    } catch (error) {
        console.error("Произошла ошибка при удалении: ", error);
        throw error;
    }
};

export const editOrg = async (orgId, updatedData) => {
    try {
        const orgDoc = doc(db, "organizations", orgId);
        await updateDoc(orgDoc, updatedData);
        console.log(`Организация с ID: ${orgId} успешно обновлена.`);
    } catch (error) {
        console.error("Произошла ошибк при обновлении: ", error);
        throw error;
    }
};

export const getOrgs = async () => {
    try {
        const orgCollection = collection(db, "organizations"); // Указываем коллекцию
        const snapshot = await getDocs(orgCollection); // Получаем данные из Firestore
        const organizations = snapshot.docs.map((doc) => ({
            id: doc.id, // ID документа
            ...doc.data(), // Данные документа
        }));
        console.log("Организации получены: ", organizations);
        return organizations; // Возвращаем массив объектов организаций
    } catch (error) {
        console.error("Произошла ошибка при получении: ", error);
        throw error;
    }
};

export const toNormalDate = milliseconds => {
    const date = new Date(milliseconds);
    const day = String(date.getDate()).padStart(2, '0');
    const month = String(date.getMonth() + 1).padStart(2, '0'); // Месяцы в JavaScript начинаются с 0
    const year = date.getFullYear();
    return `${day}.${month}.${year}`;
}

export const DDMMYY_Date = timeInMs => {
    const date = new Date(timeInMs);
    const m = date.getMonth();
    const month = (m + 1) < 10 ? '0' + (m + 1) : m + 1;
    const d = date.getDate();
    const day = d < 10 ? '0' + d : d;
    return `${date.getFullYear()}-${month}-${day}`;
}